.programs {
  @media (min-width: @tablets) {
    padding-top: 0;
    padding-bottom: 45px;
    width: @tablets;
    margin: 0 auto;
  }

  @media (min-width: @desktops) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 86px 0 70px;
    margin: 0 auto;
    width: 1220px;
  }
}

.programs__inner {
  position: relative;
  flex-grow: 1;
  width: 370px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: flex-start;

  &::before {
    content: "";
    position: absolute;
    top: 3px;
    left: 2px;
    display: block;
    flex-shrink: 0;
    width: 50px;
    height: 50px;
    margin-right: 20px;
    background-color: @green_68;
    border-radius: 50%;

    @media (min-width: @tablets) {
      content: none;
    }

    @media (min-width: @desktops) {
      content: "";
      display: block;
      width: 100px;
      height: 100px;
      top: 3px;
      left: 0;
      margin-right: 62px;
    }
  }
}

.programs__item {
  position: relative;
  display: flex;
  padding: 21px 33px 0 20px;
  background-color: @grey_f2;

  &::before {
    content: "";
    display: block;
    position: absolute;
    top: auto;
    left: auto;
    background-repeat: no-repeat;
    background-position: top left;
    z-index: 1;
  }

  @media (min-width: @tablets) {
    right: 0;
  }

  @media (min-width: @desktops) {
    width: calc(50% - 40px);
  }

  &--slim {
    position: relative;
    margin-bottom: 20px;

    &::before {
      content: "";
      width: 35px;
      height: 49px;
      top: 21px;
      left: 30px;
      background-image: url("../img/icon-cat-slim.svg");
      background-size: 100% auto;

      @media (min-width: @tablets) {
        position: absolute;
        top: 74px;
        left: 476px;
        width: 140px;
        height: 196px;
      }

      @media (min-width: @desktops) {
        width: 70px;
        height: 98px;
        top: 43px;
        left: 67px;
      }
    }

    &::after {
      @media (min-width: @tablets) {
        content: "";
        position: absolute;
        display: block;
        top: 85px;
        right: 62px;
        flex-shrink: 0;
        width: 200px;
        height: 200px;
        background-color: @green_68;
        border-radius: 50%;
      }

      @media (min-width: @desktops) {
        content: none;
      }
    }

    @media (min-width: @tablets) {
      padding: 71px 62px 53px 52px;
      margin-bottom: 30px;
    }

    @media (min-width: @desktops) {
      padding: 41px 51px 55px 52px;
      margin-bottom: 0;
    }
  }

  &--pro {
    position: relative;

    &::before {
      width: 67px;
      height: 28px;
      top: 35px;
      left: 13px;
      background-image: url("../img/icon-cat-pro.svg");
      background-size: 100% auto;

      @media (min-width: @tablets) {
        position: absolute;
        top: 135px;
        left: 412px;
        width: 268px;
        height: 112px;
      }

      @media (min-width: @desktops) {
        width: 134px;
        height: 56px;
        top: 69px;
        left: 33px;
      }
    }

    &::after {
      @media (min-width: @tablets) {
        position: absolute;
        top: 90px;
        right: 60px;
        content: "";
        display: block;
        flex-shrink: 0;
        width: 200px;
        height: 200px;
        background-color: @green_68;
        border-radius: 50%;
      }

      @media (min-width: @desktops) {
        content: none;
      }
    }

    @media (min-width: @tablets) {
      padding: 77px 28px 53px 52px;
    }

    @media (min-width: @desktops) {
      padding: 47px 61px 44px 52px;
    }
  }
}

.programs__item-title {
  position: relative;
  left: 68px;
  margin: 0;
  padding: 9px 3px;
  font-size: 24px;
  font-weight: 400;
  line-height: 37px;
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  color: @black;

  @media (min-width: @tablets) {
    left: 0;
    padding: 0;
    font-size: 36px;
    line-height: 36px;
  }

  @media (min-width: @desktops) {
    top: 32px;
    left: 160px;
  }
}

.programs__item-description {
  width: 100%;
  margin: 21px 0 22px;

  @media (min-width: @tablets) {
    margin: 0;
    margin-top: 24px;
    width: 340px;
  }

  @media (min-width: @desktops) {
    width: 100%;
    margin-top: 0;
    padding-top: 112px;
  }
}

.programs__item--pro .programs__item-description {
  @media (min-width: @desktops) {
    padding-top: 104px;
  }
}

.programs__item-link {
  width: 100%;
  position: relative;
  display: inline-block;
  padding: 20px 126px 20px 0;
  font-size: 16px;
  line-height: 16px;
  font-family: "Oswald", "Arial", sans-serif;
  color: @black;
  text-transform: uppercase;
  text-decoration: none;
  box-shadow: inset 0 1px @grey_d9;

  &::after {
    content: "";
    position: absolute;
    display: block;
    top: 21px;
    left: 101px;
    width: 24px;
    height: 12px;
    background-image: url("../img/icon-programs-right-arrow.svg");
    background-repeat: no-repeat;
    background-size: 32px 11px;
    background-position: right center;
    transition: right 0.3s ease, width 0.3s ease;

    @media (min-width: @tablets) {
      top: 12px;
      left: 134px;
    }

    @media (min-width: @desktops) {
      top: 12px;
      left: 130px;
    }
  }

  @media (min-width: @tablets) {
    padding: 11px 267px 11px 0;
  }

  &:hover::after {
    right: 0;
    width: 32px;
  }

  &:active {
    opacity: 0.3;
  }

  @media (min-width: @tablets) {
    margin-top: 48px;
    font-size: 20px;
    box-shadow: none;
  }

  @media (min-width: @desktops) {
    margin-top: 23px;
  }
}

.programs__item--slim .programs__item-link {
  @media (min-width: @desktops) {
    &::after {
      left: 137px;
    }
  }
}
