.button {
  display: inline-block;
  width: 100%;
  padding: 10px 22px;
  font-size: 16px;
  line-height: 20px;
  font-family: "Oswald", "Arial", sans-serif;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: @white;
  background-color: @green_68;
  border: none;

  &:hover {
    background-color: @green_5e;
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }
}
